import { Redirect, Route, Switch, withRouter } from "react-router-dom";

import NavBack from './components/NavBack/index'
import React from "react";
import { routes } from "./router";

function App({ location }) {

	return (
		<NavBack>
			<Switch>
				{routes.map((route, idx) =>
					route.children ? (
						route.children.map((c, i) => (
							<Route
								key={c.path}
								path={c.path}
								exact={c.exact}
								render={(props) => (
									<c.component {...props} router={c.routes} />
								)}
							/>
						))
					) : (
						<Route
							key={route.path}
							path={route.path}
							exact={route.exact}
							render={(props) => (
								<route.component {...props} router={route.routes} />
							)}
						/>
					)
				)}

				<Redirect from="/" to="/hacker" exact={true} />
			</Switch>
		</NavBack>
	);
}

export default withRouter(App);
