import './index.css'

import { useHistory, useLocation, } from 'react-router'

import { AppstoreOutline } from 'antd-mobile-icons'
import { NavBar, } from 'antd-mobile'
import React from 'react'

const NavBack = (props) => {
    const history = useHistory()
    const location = useLocation()
    const isHome = location.pathname === '/'

    const back = () => {
        history.goBack()
    }
    return (
        <div className='flex-col nav-wrap' >
            <div className='head-nav-bar'>
                <NavBar back={isHome ? undefined : ''} backArrow={isHome ? false : true} onBack={back} right={isHome ? null : <AppstoreOutline onClick={() => history.replace('/')} />}>
                    
                </NavBar>
            </div>
            <div className='flex-1 ui-ovy-a'>
                {props.children}
            </div>
        </div >
    )
}

export default NavBack